import React from "react";
import { render } from "react-dom";
import {
  Link,
  Element,
  Events,
  animateScroll as scroll,
  scroller
} from "react-scroll";
import "./Home.scss";

const basename = process.env.PUBLIC_URL;

class Home extends React.Component {
  render() {
    return (
      <div>
        <div className="navbar__container">
          <img
            className="navbar__logo"
            src={`${basename}/images/header.svg`}
            alt="logo"
          />
          <ul className="navbar__list">
            <li className="navbar__list-item">
              <Link
                className="navbar__list-button"
                activeClass="active"
                to="description"
                spy={true}
                smooth={true}
                duration={500}
              >
                Descripción
              </Link>
            </li>
            <li className="navbar__list-item">
              <Link
                className="navbar__list-button"
                activeClass="active"
                to="dates"
                spy={true}
                smooth={true}
                duration={500}
              >
                Fechas importantes
              </Link>
            </li>
            <li className="navbar__list-item">
              <Link
                className="navbar__list-button"
                activeClass="active"
                to="map"
                spy={true}
                smooth={true}
                duration={500}
              >
                Mapa
              </Link>
            </li>
          </ul>
        </div>

        <section className="section__container section__container--hero">
          <div className="hero__content">
            <div className="hero__content-first">
              <div className="hero__content-square">
                <span>LA CONFERENCIA</span>
                <span>LATINOAMERICANA DE </span>
                <span>PYTHON CIENTÍFICO 2019</span>
                <span>
                  <strong>SE REALIZARÁ EN COLOMBIA</strong>
                </span>
              </div>
            </div>
            <div className="hero__content-second">
              <ul className="sponsors__list">
                <li className="sponsors__list-item">
                  <img
                    className="sponsors__image"
                    src={`${basename}/images/sponsors/andes.svg`}
                    alt="logo"
                  />
                </li>
                <li className="sponsors__list-item">
                  <img
                    className="sponsors__image"
                    src={`${basename}/images/sponsors/scipy_latam.png`}
                    alt="logo"
                  />
                </li>
                <li className="sponsors__list-item">
                  <img
                    className="sponsors__image"
                    src={`${basename}/images/sponsors/ean.png`}
                    alt="logo"
                  />
                </li>
                <li className="sponsors__list-item">
                  <img
                    className="sponsors__image"
                    src={`${basename}/images/sponsors/python.png`}
                    alt="logo"
                  />
                </li>
                <li className="sponsors__list-item">
                  <img
                    className="sponsors__image"
                    src={`${basename}/images/sponsors/latam_tech.png`}
                    alt="logo"
                  />
                </li>
              </ul>
            </div>
          </div>
          <div className="hero__image-container">
            <img
              className="hero__image"
              src={`${basename}/images/hero_image.svg`}
              alt="Hero"
            />
          </div>
        </section>
        <Element name="description" className="element">
          <section className="section__container">
            <div className="section__container-content">
              <p>
                El evento busca compartir experiencias alrededor del uso de
                Python científico a nivel académico y en empresas.
              </p>
              <p>
                Travis Oliphant, creador de Numpy y Scipy, ex-gerente de
                Anaconda y gerente de Quansight, será uno de los keynotes del
                evento.
              </p>
              <p>
                El evento tendrá llamado de abstracts científicos para charlas
                académicas, industriales, tutoriales y sprints.
              </p>
            </div>
            <div className="section__container-image">
              <img
                className="section__image"
                src={`${basename}/images/description.svg`}
                alt="Hero"
              />
            </div>
          </section>
        </Element>
        <Element name="dates" className="element">
          <section className="section__container">
            <div className="section__important-dates">
              <h2 className="section__title"> Fechas importantes </h2>
              <div className="section__dates">
                <p>
                  <span className="section__dates-deadline">
                    Call for papers tutorials:
                  </span>
                  Abril 15
                </p>
                <p>
                  <span className="section__dates-deadline">
                    Call for papers special tracks:
                  </span>
                  Abril 15
                </p>
                <p>
                  <span className="section__dates-deadline">
                    Call for papers talks:
                  </span>
                  Abril 25
                </p>
                <p>
                  <span className="section__dates-deadline">
                    Early inscription:
                  </span>
                  Agosto 10
                </p>
                <p>
                  <span className="section__dates-deadline">Evento:</span>8 al
                  10 Octubre 2019
                </p>
              </div>
            </div>
          </section>
        </Element>
        <Element name="map" className="element">
          <section className="section__container">
            <div className="section__important-dates">
              <h2 className="section__title"> Mapa</h2>
              <div class="content-section">
                <img
                  src={`${basename}/images/map.png`}
                  class="img-responsive"
                  alt="Map of the event"
                />
              </div>
            </div>
          </section>
        </Element>
      </div>
    );
  }
}

export default Home;



