import React, { Component } from "react";
import { Switch, Route } from "react-router-dom";
import { Home } from "Pages";
import "./App.scss";

class App extends Component {
  render() {
    return (
      <div>
        <Switch>
          <Route exact path="/" component={Home} />
          <Route path="/Home" component={Home} />
        </Switch>
      </div>
    );
  }
}

export default App;



